
.work-window {
  margin: 15px 5px 5px 5px;
}
.category {
  margin: 15px 5px 5px 5px;
  height: 100%;
  width: 10%;
  position: fixed;
  z-index: 1;
  left: 0px;
}
.content {
  margin-left: 12%;
}
body {
  font: normal 14px "Trebuchet MS", Helvetica, sans-serif;
  margin: 0;
}
.fullscreen {
  width: 95%;
  height: 100%;
  z-index: 1;
  overflow-x: hidden;
  margin-top : 15px;
}
.split {
  width: 50%;
  height: 100%;
  position: fixed;
  z-index: 1;
  overflow-x: hidden;
  margin-top : 15px;
}
.right {
  width: 36%;
  right: 0px;
  padding-right: 10px;
}
.selected {
 background-color: aliceblue;
}


.selected {
 background-color: aliceblue;
}
.done {
 color: grey;
}
.done ul {
 text-decoration: line-through
}
.empty {
  color : white;
  background-color : #f0f0f5;
}
.input {
  padding: 5px 0px 5px 5px;
  font-size: medium;
}
.title {
  width: 99%;
  margin: 0px 5px 5px 0px;
  border-radius: 5px;
  border-width: 0px;
}
.desc {
  width: 99%;
  margin: 0px 5px 5px 0px;
  border-radius:5px;
  border-width:1px;
}
.listTask .task-row {
  padding : 2px 2px;
  border-bottom: 1px solid;
  border-width: 1px;
}
.task-desc {
  font-size: 13;
  padding-left: 10px;
  color: grey;
}
.task-title {
  font-size: 15;
}


.element {
  padding: 10px 0px 10px 10px;
  border-radius: 5px;
  width : 100%;
}
.category-editor {
  padding: 10px 0px 30px 10px;
  border-radius: 5px;
  width : 100%;
}
.category-remove-button {
  float:right;
  margin:5px 5px 5px 5px;
}
.edited {
  padding: 5px 5px 5px 5px;
  border-radius: 5px;
  width : 80%;
}


.i-category {
  margin: 5px 0px 0px 5px;
}

